<template>
    <div>
        <h3>Selected Users</h3>
        <div class="ui divided items">
            <div class="item"
                v-for="item in users"
                :key="item.id"
            >
                <div class="ui tiny image">
                    <img :src="item.avatar_url">
                </div>
                <div class="content">
                    <a class="header">{{ item.login }}</a>
                    <div class="meta">
                        <span>selected</span>
                    </div>
                    <button class="ui red labeled icon button" @click="deleteUser(item.id)">
                        <i class="trash icon"></i>
                        Delete
                    </button>
                </div>
            </div>
        </div>
        <button class="ui red labeled icon button" @click="deleteFullList">
            <i class="trash icon"></i>
            Delete full list
        </button>
    </div>
</template>

<script>
export default {
    props: {
        users: Array
    },
    data: function() {
        return {

        }
    },
    methods: {
        onSelectUser(login) {
            this.$emit('onSelectUser', login);
        },
        deleteFullList() {
            this.$emit('deleteFullList');
        },
        deleteUser(id) {
            this.$emit('deleteUser', id);
        }
    }
}
</script>   