<template>
    <div>
        <h3>Users</h3>
        <div class="ui divided items app-users">
            <div class="item"
                v-for="item in users"
                :key="item.id"
                @click="onSelectUser(item.login)">
                <div class="ui tiny image">
                    <img :src="item.avatar_url">
                </div>
                <div class="content">
                    <a class="header">{{ item.login }}</a>
                    <div class="meta">
                        <span>link</span>
                    </div>
                    <div class="description">
                        <p></p>
                    </div>
                    <div class="extra">
                        <a :href="item.html_url" target="_blank">{{ item.html_url }}</a>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        users: Array
    },
    methods: {
        onSelectUser(login) {
            this.$emit('onSelectUser', login);
        }
    }
}
</script>